import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import axoisPost from './assets/javascript/axios'
import moment from './static/js/moment-with-locales.min.js';
import jq from './static/js/jquery-1.10.2.min.js';

Vue.use(ElementUI);
Vue.prototype.$POST = axoisPost.axiosPost;
Vue.prototype.$moment = moment;
Vue.prototype.$ = jq;
Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
