<template>
	<div style="background-color: white;">
		
		<div class="layui-fluid">
			<div class="layui-card myBorder">
				<div class="divPad">
					<div class="layui-inline myTitle mr15">我的商品</div>
					<div class="layui-inline">
						<label class="layui-form-label">维护商品类型：</label>
						<div class="layui-form layui-input-inline" style="width: 120px;">
							<select name="interest" lay-filter="sel" lay-event="sel">
								<option value="0" checked>待维护商品</option>
								<option value="1">已维护商品 </option>
							</select>
						</div>
					</div>
					<button class="layui-btn layui-btn-sm" id="dl1">导出</button>
				</div>
		
				<table lay-filter="demoMy" id="tableMy"></table>
			</div>
		
			<div class="layui-card myBorder">
				<div class="divPad">
					<div class="layui-inline myTitle mr15">竞品</div>
					<div class="layui-inline">
						<label class="layui-form-label">维护商品类型：</label>
						<div class="layui-form layui-input-inline" style="width: 120px;">
							<select name="interest" lay-filter="selJP" lay-event="sel">
								<option value="0" checked>待维护商品</option>
								<option value="1">已维护商品 </option>
							</select>
						</div>
					</div>
					<button class="layui-btn layui-btn-sm" id="dl2">导出</button>
				</div>
		
				<table lay-filter="demoJP" id="myTableJP"></table>
			</div>
			
			<div class="layui-card myBorder">
				<div class="divPad" style="border-bottom: 1px solid #CCCCCC;">
					<div class="layui-inline myTitle mr15">数据上传</div>
				</div>
				
				<div class="time">
					<div class="name">商品类型选择：</div>
					<div class="time-value">
						<el-select class="input-value" v-model="goodsType" placeholder="请选择" @change="goodsTypeChange">
							<el-option value="my" label="我的商品"></el-option>
							<el-option value="competitor" label="竞品"></el-option>
						</el-select>
					</div>
					<div class="clear"></div>
				</div>
				
				<div class="time">
					<div class="name">数据平台选择： </div>
					<div class="time-value">
						<el-select class="input-value" v-model="platformValue" placeholder="请选择" @change="plaChange">
							<el-option v-for="(item, index)  in platformArr" :key="index" :label="item" :value="index"></el-option>
						</el-select>
					</div>
					<div class="clear"></div>
				</div>
				
				<div class="time">
					<div class="name">数据类型选择： </div>
					<div class="time-value">
						<el-select v-model="sendUrl" placeholder="请选择" class="input-value" @change="UrlChange">
							<el-option v-for="(item, index) in urlArr" :key="item.title" :label="item.title" :value="item.url">
							</el-option>
						</el-select>
						<div class="clear"></div>
					</div>
					<div class="clear"></div>
				</div>
				
				<div class="time" v-if="ifShop">
					<div class="name">数据店铺选择： </div>
					<div class="time-value">
						<el-select class="input-value" v-model="shopValue" placeholder="请选择">
							<el-option v-for="item in shopOptions" :key="item.id" :label="item.shop_name" :value="item.id" v-if="item.platform == platformValue"></el-option>
						</el-select>
					</div>
					<div class="clear"></div>
				</div>
				
				<div class="time" v-if="ifZd">
					<div class="name">数据终端选择： </div>
					<div class="time-value">
						<el-select class="input-value" v-model="zhongduanValue" placeholder="请选择">
							<el-option v-for="(item, index)  in zhongduanArr" :key="index" :label="item" :value="index"></el-option>
						</el-select>
					</div>
					<div class="clear"></div>
				</div>
				
				
				<div class="time" v-if="ifDate">
					<div class="name">数据日期区间：</div>
					<div class="time-value">
						<div class="dateAll floatLeft" style="margin-left: 10px;">
							<div class="dateArea">
								<span class="word wordClick" data-type="date" v-on:click="selectLis('daterange', 1)">日</span>
								<span class="word" data-type="week" v-on:click="selectLis('week', 7)">周</span>
								<span class="word" data-typea="month" v-on:click="selectLis('monthrange', 31)">月</span>
								<div class="clear"></div>
							</div>
							<div class="datePicker" id="datePicker1">
								<el-date-picker :picker-options="pickOption" popper-class="aaa" v-model="daterange" class="dateInput" type="daterange"
								 value-format="yyyy-MM-dd" format="yyyy-MM-dd" placeholder="选择日期" v-on:change="dateClick($event)" style="width: 70px;height: 0;">
								</el-date-picker>
								<el-date-picker :picker-options="pickOption" id="el-week" popper-class="aaa" v-model="dateValue" class="dateInput"
								 type="daterange" value-format="yyyy-MM-dd" format="yyyy-MM-dd" placeholder="选择日期" v-on:change="dateClick($event)"
								 style="width: 70px;height: 0;">
								</el-date-picker>
								<el-date-picker :picker-options="pickOption" id="el-monthrange" popper-class="aaa" v-model="monthrange" class="dateInput"
								 type="monthrange" value-format="yyyy-MM-dd" format="yyyy-MM-dd" placeholder="选择日期" v-on:change="dateClick($event)"
								 style="width: 70px;height: 0;">
								</el-date-picker>
								<div class="clear"></div>
							</div>
						</div>
				
						<div class="selectShow floatLeft">
							<span class="calendar-msg">{{ selectDateShow }}</span>
						</div>
					</div>
					<div class="clear"></div>
				</div>
				
				<div class="time">
					<div class="name">数据文件选择： </div>
					<div class="time-value">
						<input class="input-value" type="file" id="fileInput" @change="fileLis">
					</div>
					<div class="clear"></div>
				</div>
				
				<div class="time">
					<div class="name">操作：</div>
					<div class="time-value">
						<el-button @click="upload()" type="success" plain>提交</el-button>
					</div>
					<div class="clear"></div>
				</div>
				
				<!-- 表格区域 -->
				<el-table border striped class="table" :data="tableDataShow" style="width: 100%" :height="tableHeight">
					<el-table-column align="center" v-for="(item, index, attr) in tableHead" :key="attr" :prop="index" :label="item"
					 width="180"></el-table-column>
				</el-table>
			</div>
			
		</div>
		
		

		<div class="myFoot">广东小冰火人网络科技有限公司 版权所有</div>
	</div>
</template>

<script>
	import XLSX, {
		read
	} from "xlsx"
	import jschardet from "jschardet"
	import papaparse from "papaparse"
	export default {
		name: 'Home.vue',
		data: function() {
			return {
				baseIp: '',
				phpTimes: [], // php反馈需要识别的时间
				shopOptions: [], // 店铺选项
				shopValue: '', // 店铺选择的值
				phpUrls: [], // php反馈可以提交的地址
				isCanTime: true, // 能否发送时间
				customInput: '', // 时间
				tableData: [], // 表身数据
				tableDataShow: [], // 表身显示数据
				tableHead: {}, // 表格头部
				// tableHeight: window.innerHeight - 280, // 表格的高度
				tableHeight: 500,
				hasFile: 0, // 是否有文件
				tableHeadArr: [], // 表格头部的数组形式
				platformArr: [], //平台数据
				platformValue: '', // 平台值
				excel_name: '',

				between_start: '',
				between_end: '',
				remove_rule: '',

				zhongduanArr: [], //终端数据
				zhongduanValue: '', // 终端值
				timeSeArr: [],
				timeSeValue: '',

				send: {
					date: '',
					date_type: '1',
				},

				// 日期控制的变量  开始 ============================
				time_data: {
					start_time: "",
					end_time: ""
				},
				dateValue: '',
				dateType: "daterange",
				selectDate: '---',
				defaultValue: '',
				dataType: 1,
				pickOption: {
					firstDayOfWeek: 1,
					disabledDate: function(time) {}
				},
				selectDateShow: '',
				daterange: [],
				monthrange: [],
				// 日期控制的变量  结束 ============================

				sendUrl: '',
				goodsType: '',
				ifShop: '',
				ifZd: '',
				ifDate: '',
				urlArr: []
			}
		},
		mounted: function() {
			this.getIp();
			this.getData();

			var _this = this;
			var form = layui.form;
			form.render()
			form.on('select(sel)', function(data) {
				var sel = data.value;
				_this.getData1(sel)
			});
			form.on('select(selJP)', function(data) {
				var sel = data.value;
				_this.getDataJP(sel)
			});

			var sel = 0;
			this.getData1(sel)
			this.getDataJP(sel)
		},
		methods: {
			// 日周月被点击  打开日期选择框
			selectLis: function(type, dataType) {
				var $ = this.$;

				this.dataType = dataType;
				var data = this.time_data;

				if (this.dateType != type) {
					this.dateValue = '';
					this.dateType = type;
				}

				if (type == 'daterange') {
					$('#datePicker1').find('.el-date-editor--daterange')[0].click();
				}
				if (type == 'week') {
					$('#datePicker1').find('.el-date-editor--daterange')[1].click();
					$('.available').removeClass('in-range start-date');
					$('.available').removeClass('in-range end-date');
				}
				if (type == 'monthrange') {
					$('#datePicker1').find('.el-date-editor--monthrange').click()
				}
			},

			// 时间被点击
			dateClick: function(value) {
				var $ = this.$;
				var moment = this.$moment;
				$('.wordClick').removeClass('wordClick');

				if (this.dateType == 'week') {
					$($('.word')[1]).addClass('wordClick');
					this.send.date_type = 2;
					var start = moment(value[0]).isoWeekday(1).week(moment(value[0]).isoWeekday(1).week()).startOf('isoweek').format(
						'YYYY-MM-DD');
					var end = moment(value[1]).isoWeekday(1).week(moment(value[1]).isoWeekday(1).week()).endOf('isoweek').format(
						'YYYY-MM-DD');
					this.selectDateShow = start + '_' + end;
					this.send.date = this.selectDateShow;
				}
				if (this.dateType == 'daterange') {
					$($('.word')[0]).addClass('wordClick');
					this.selectDateShow = value[0] + '_' + value[1];
					this.send.date = this.selectDateShow;
					this.send.date_type = 1;
				}
				if (this.dateType == 'monthrange') {
					$($('.word')[2]).addClass('wordClick');
					var start = moment(value[0]).month(moment(value[0]).month()).startOf('month').format('YYYY-MM-DD');
					var end = moment(value[1]).month(moment(value[1]).month()).endOf('month').format('YYYY-MM-DD');
					var show = start + '_' + end;
					this.selectDateShow = value[0].substr(0, 7) + '_' + value[1].substr(0, 7);
					this.send.date = show;
					this.send.date_type = 3;
				}

				this.daterange = '';
				this.dateValue = '';
				this.monthrange = '';
			},


			// 获取当前ip
			getIp: function() {
				var ip = window.location.host;
				ip = 'http://' + ip;
				ip = process.env.NODE_ENV === "development" ? "/apis" : ip;
				this.baseIp = ip;
			},

			// 获取基础数据
			getData: function() {
				// let url = this.baseIp + '/thinkphp/Platform/Data/site_time';
				let url = this.baseIp + '/thinkphp/MiniicePlatform/Data/site_time';
				this.$POST(url, '').then(this.getDataHandle);
			},



			// 获取基础数据后的处理方法
			getDataHandle: function(res) {
				this.phpTimes = res.data.time.slice(0)
				this.phpUrls = res.data.url;
				this.shopOptions = res.data.prd;
				this.orShopArr = res.data.prd;
				this.platformArr = ['无'].concat(res.data.platform);

				var between_rule = this.changeExStr(res.data.handle.between[0].rule);
				var remove_rule = res.data.handle.remove[0].rule;
				this.between_start = between_rule.substr(0, 2);
				this.between_end = between_rule.substr(between_rule.length - 1, 1)
				this.remove_rule = remove_rule;

				this.zhongduanArr = res.data.terminal;
				this.timeSeArr = res.data.date_type;

				// this.phpUrls = [{
				// 		"title": "淘宝-店铺数据-日",
				// 		"url": "/thinkphp/MiniicePlatform/Data/test1/",
				// 		"platform": "1",
				// 		"who": "my",
				// 		"option": "shop"
				// 	},
				// 	{
				// 		"title": "竞品商品明细（淘系+猫宁）",
				// 		"url": "/thinkphp/MiniicePlatform/Data/test19/",
				// 		"platform": "0",
				// 		"who": "competitor",
				// 		"option": ""
				// 	}
				// ];

			},

			//转换字符
			changeExStr(str) {
				var d = document.createElement('div');
				d.innerHTML = str;
				return d.innerText;
			},

			// 文件发生改变
			fileLis: function(event) {
				this.hasFile = 1;
				this.readFile(event)
			},

			// 读取文件内容
			readFile: function(event) {
				let file = event.target.files[0];
				this.excel_name = file.name;
				if (file != undefined) {
					let reader = this.newFileReader(file);
					if (file.name.split(".").reverse()[0] == 'csv') { // csv文件
						reader.readAsDataURL(file);
					} else { // excel文件
						reader.readAsBinaryString(file);
					}
				}
			},

			// 读取csv文件
			readCsv: function(file, encoding) {
				let _this = this;
				papaparse.parse(file, {
					encoding: encoding,
					complete: function(results) {
						results = results.data;
						let arr = [];
						let head = [];
						for (let i in results[0]) {
							head.push(results[0][i]);
						}
						for (let i = 0; i < results.length - 1; i++) {
							let obj = {};
							for (let j in head) {
								obj[head[j]] = results[i][j];
							}
							arr.push(obj);
						}
						let index = _this.processingData(arr);
						if (index == -1) {
							index = 0;
						}
						_this.makeTable(arr, index);
					}
				})
			},

			// 注册一个文件读取器
			newFileReader: function(file) {
				const loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(255, 255, 255, 0.7)'
				});
				let suffix = file.name.split(".").reverse()[0];
				let _this = this;
				let reader = new FileReader();
				reader.onload = function(event) {
					loading.close();
					let data = event.target.result;
					if (suffix == 'csv') { // csv
						data = _this.checkEncoding(data);
						_this.readCsv(file, data);
					} else { // excel
						let excelData = XLSX.read(data, {
							type: 'binary',
							// cellDates: true,
						}); // 读取完成的数据
						const sheet2JSONOpts = {
							defval: ''
						};
						let excelJson = XLSX.utils.sheet_to_json(excelData.Sheets[excelData.SheetNames[0]], sheet2JSONOpts);
						let index = _this.processingData(excelJson);
						_this.makeTable(excelJson, index);
					}
				}
				return reader;
			},

			// csv检查编码
			checkEncoding: function(base64Str) {
				var str = atob(base64Str.split(";base64,")[1]); //这种方式得到的是一种二进制串
				var encoding = jschardet.detect(str); //要用二进制格式
				encoding = encoding.encoding;
				if (encoding == "windows-1252") { //有时会识别错误（如UTF8的中文二字）
					encoding = "ANSI";
				}
				return encoding;
			},

			// 制做字母
			makezimus: function(length) {
				let allArr = [65]; // 位数的详细
				let arr = [];
				for (let i = 0; i < length; i++) {
					let zimu = '';
					for (let j = allArr.length - 1; j >= 0; j--) {
						zimu = zimu + String.fromCharCode(allArr[j]);
					}
					arr.push(zimu);
					allArr[0]++;
					for (let j = 0; j < allArr.length; j++) {
						if (allArr[j] == 91) {
							allArr[j] = 65;
							if (allArr[j + 1] == undefined) {
								allArr.push(65);
							} else {
								allArr[j + 1]++;
							}
						}
					}
				}
				return arr[arr.length - 1];
			},

			// 处理数据
			processingData: function(data) {
				// console.log(data)
				for (let i in data) {
					let isHasEmpty = 0;
					let head = {};
					let headArr = [];
					// var nn = 0;
					for (let j in data[i]) {
						if (i == 0) {
							// console.log(1)
							head[j] = j;
							headArr.push(j);
						} else {
							// console.log(2)
							head[j] = data[i][j];
							headArr.push(data[i][j]);
						}

						if (data[i][j] == '' && j == '__EMPTY') {
							isHasEmpty = 1;
							break;
						}

						// nn++;
					}
					if (isHasEmpty == 0) {
						this.tableHeadArr = headArr;
						this.tableHead = head;
						return i > 0 ? i : -1;
					}
				}
				this.$message.error('请删除文件的 ' + this.makezimus(Object.keys(data[0]).length) + '列 后再上传!');
			},

			//过滤
			checkRule(tt) {
				let str1 = tt.toString();
				if (str1) {
					if (str1.indexOf(',') > -1) {
						let num = str1;
						str1 = num.replace(new RegExp(this.remove_rule, 'g'), '')
						// str1 = num.replace(/\,/g, '');
					}
					if (str1.substr(0, 2) == this.between_start && str1.substr(str1.length - 1, 1) == this.between_end) {
						let str = str1.replace(this.between_start, '');
						str = str.replace(this.between_end, '');
						str1 = str;
					}
				}
				return str1
			},

			// 建立表格
			makeTable: function(data, index) {
				var _this = this;
				let table = [this.tableHeadArr];
				index = parseFloat(index) + 1;
				for (let i = index; i < data.length; i++) {
					let arr = [];
					for (let j in data[i]) {
						if (data[i][j]) {
							if (this.phpTimes.toString().indexOf(this.tableHead[j]) != -1) {
								data[i][j] = this.ExcelDateToJSDate(data[i][j]);
							}
							data[i][j] = _this.checkRule(data[i][j]);
						}
						arr.push(data[i][j]);
					}

					if (arr.length > 0 && arr.join('') != '') {
						table.push(arr);
					}
				}

				this.tableDataShow = [this.tableHead];
				this.tableDataShow = data.slice(index, 20);
				this.tableData = table;
				// console.log(this.tableData)
			},

			// 转换时间
			ExcelDateToJSDate: function(serial) {
				try {
					let arr = serial.toString();
					// if(arr.indexOf('~')>-1){
					// 	var sp = arr.split('~');
					// 	var date = sp[0].trim()+'_' +sp[1].trim();
					// 	return date;
					// }
					if (arr.indexOf('~') > -1) {
						var sp = arr.split('~');
						var date = sp[0].trim() + '_' + sp[1].trim();
						return date;
					}
					if (arr.indexOf('至') > -1) {
						var sp = arr.split('至');
						var date = sp[0].trim() + '_' + sp[1].trim();
						return date;
					}
					if (arr.indexOf('/') > -1) {
						var sp = arr.split('/');
						var date = sp[0].trim() + '_' + sp[1].trim();
						return date;
					}
					if (arr.substr(0, 1) == 4) {
						var moment = this.$moment;
						arr = moment('1900-01-01').add(arr - 2, 'days').format('YYYY-MM-DD');
						return arr;
					}
					if (arr.indexOf('-') == -1) {
						var new_date = arr;
						var year = new_date.substring(0, 4);
						var month = new_date.substring(4, 6);
						var day = new_date.substring(6, 8);
						arr = year + '-' + month + '-' + day;
						if (arr.length == 8) {
							var start = arr + '01';
							var moment = this.$moment;
							var end = moment(start).month(moment(start).month()).endOf('month').format('YYYY-MM-DD');
							var date = start + '_' + end;
							return date;
						}
						return arr;
					}
					if (arr.split('-').length != 3) {
						let utc_days = Math.floor(serial - 25569);
						let utc_value = utc_days * 86400;
						let date_info = new Date(utc_value * 1000);
						let fractional_day = serial - Math.floor(serial) + 0.0000001;
						let total_seconds = Math.floor(86400 * fractional_day);
						let seconds = total_seconds % 60;
						total_seconds -= seconds;
						let hours = Math.floor(total_seconds / (60 * 60));
						let minutes = Math.floor(total_seconds / 60) % 60;
						let AssemblyTime = new Date(date_info.getFullYear(), date_info.getMonth(), date_info.getDate());
						return [AssemblyTime.getFullYear(), AssemblyTime.getMonth() + 1 < 10 ? '0' + (AssemblyTime.getMonth() + 1).toString() :
							(AssemblyTime.getMonth() + 1).toString(), AssemblyTime.getDate() < 10 ? '0' + (AssemblyTime.getDate()).toString() :
							(AssemblyTime.getDate()).toString()
						].join('-')
					}
				} catch (error) {}
				return serial;
			},

			// 上传数据
			upload: function() {
				var _this = this;
				var $ = _this.$;
				if (this.goodsType == '') {
					this.$message.error('请选择商品类型！！！');
					return;
				}
				var url = this.sendUrl;
				if (url == '') {
					this.$message.error('请选择数据类型！！！');
					return;
				};
				let send = {
					data_type: this.send.date_type,
					time: this.send.date,

					platform: this.platformValue,
					excel_name: this.excel_name,
					terminal: this.zhongduanValue,
					shop_id: this.shopValue
				};

				send['data'] = JSON.stringify(this.tableData);
				if (this.shopValue == '' && this.ifShop == true) {
					this.$message.error('请选择店铺！！！');
					return;
				}
				if (this.hasFile == 0) {
					this.$message.error('请选择上传的表格！！！');
					return;
				}

				var sgoodsType = '';
				if (this.goodsType == 'my') {
					sgoodsType = '我的商品';
				}
				if (this.goodsType == 'competitor') {
					sgoodsType = '竞品';
				}

				var splatformValue = '';
				this.platformArr.forEach(function(item, index) {
					// var nindex = index + 1;
					var nindex = index;
					if (nindex == _this.platformValue) {
						splatformValue = item;
					}
				})

				var sdaType = '';
				this.phpUrls.forEach(function(item, index) {
					if (item.url == _this.sendUrl) {
						sdaType = item.title;
					}
				})

				var sshop = '';
				this.shopOptions.forEach(function(item, index) {
					if (item.id == _this.shopValue) {
						sshop = item.shop_name;
					}
				})

				var szd = '';
				this.zhongduanArr.forEach(function(item, index) {
					if (index == _this.zhongduanValue) {
						szd = item;
					}
				})

				var time = '';
				var sdateType = '';
				if (this.send.date_type == 1) {
					sdateType = '日'
				}
				if (this.send.date_type == 2) {
					sdateType = '周'
				}
				if (this.send.date_type == 3) {
					sdateType = '月'
				}
				time = sdateType + ' ' + this.send.date;

				var str1 = ``;
				if (this.ifShop) {
					str1 = `<div class="layui-form-item">数据店铺：` + sshop + `</div>`
				}
				var str2 = ``;
				if (this.ifZd) {
					str2 = `<div class="layui-form-item">数据终端：` + szd + `</div>`
				}
				var str3 = ``;
				if (this.ifDate) {
					str3 = `<div class="layui-form-item">数据日期：` + time + `</div>`
				}
				var str =
					`<form class="layui-form layui-field-box mt15">
		<div class="layui-form-item">商品类型：` + sgoodsType +
					`</div>
		<div class="layui-form-item">数据平台：` + splatformValue + `</div>
		<div class="layui-form-item">数据类型：` +
					sdaType + `</div>` +
					str1 + str2 + str3 +
					`<div class="layui-form-item">数据文件：` + this.excel_name + `</div>` +
					`<div class="layui-form-item" style="text-align: center;">
				<button class="layui-btn" type="button" id="sure">确定</button>
		</div>
		</form>`;
				layer.open({
					type: 1,
					title: '',
					content: str,
					area: ['350px', '300px'],
					id: 'LAY_layuipro',
				});

				$('#sure').unbind('click').bind('click', function() {
					// console.log(url)
					// console.log(send)
					_this.$POST(url, send).then(_this.uploadHandle);
				})

			},

			// 上传数据后的处理函数
			uploadHandle: function(res) {
				this.$message({
					message: '上传数据成功',
					type: 'success'
				});
				this.tableData = []; // 表身数据
				this.tableDataShow = []; // 表身显示数据
				this.tableHead = []; // 表格头部
				this.hasFile = 0; // 是否有文件
				document.getElementById('fileInput').value = '';

				this.getList();
			},

			goodsTypeChange(data) {
				this.platformValue = '';
				this.shopValue = '';
				this.sendUrl = '';

				this.urlArr = [];
				var _this = this;
				for (var i in _this.phpUrls) {
					var d = _this.phpUrls[i];
					if (d.who == _this.goodsType) {
						this.urlArr.push(d)
					}
				}
			},

			plaChange() {
				this.shopValue = '';
				this.sendUrl = '';

				this.urlArr = [];
				var _this = this;
				for (var i in _this.phpUrls) {
					var d = _this.phpUrls[i];
					if (d.who == _this.goodsType && d.platform == _this.platformValue) {
						this.urlArr.push(d)
					}
				}
			},

			UrlChange(data) {
				var _this = this;
				_this.ifShop = false;
				_this.ifDate = false;
				_this.ifZd = false;
				_this.shopValue = '';
				_this.send.date_type = '';
				_this.send.date = '';
				_this.selectDateShow = '';
				_this.zhongduanValue = '';
				for (var i in this.phpUrls) {
					var d = this.phpUrls[i];
					if (d.url == data) {
						var option = d.option.split(',');
						for (var j in option) {
							if (option[j] == 'shop') {
								_this.ifShop = true;
							}
							if (option[j] == 'date') {
								_this.ifDate = true;
							}
							if (option[j] == 'terminal') {
								_this.ifZd = true;
							}
						}
					}
				}
			},


			getData1(sel, ci) {
				var _this = this;
				addLoading();
				$.ajax({
					url: url_proSelect,
					type: 'post',
					data: {
						complete: sel
					},
					dataType: 'json',
					timeout: 60000,
					success: function(res) {
						removeLoading()
						checkStatus(res, function() {
							// console.log(res.data)
							_this.fillTable(res.data)
						});
					},
					error: function() {
						removeLoading()
						layer.alert('请求失败')
					}
				});
			},

			getDataJP(sel, ci) {
				var _this = this;
				addLoading();
				$.ajax({
					url: url_jpGoods,
					type: 'post',
					data: {
						complete: sel
					},
					timeout: 60000,
					dataType: 'json',
					success: function(res) {
						removeLoading()
						checkStatus(res, function() {
							// console.log(res.data)
							_this.fillTableJP(res.data)
						});
					},
					error: function() {
						removeLoading()
						layer.alert('请求失败')
					}
				});
			},

			fillTable(data) {
				var thead = '';
				var a = 0;
				var arr_title = [];
				for (var i in data[0]) {
					thead += `<th lay-data="{field:` + a + `}">` + i + `</th>`;
					a++;
					arr_title.push(i)
				}
				thead = `<thead><tr>` + thead + `</tr></thead>`;

				var tbody = '';
				for (var i in data) {
					var d = data[i];
					var str = '';
					for (var j in d) {
						str += `<td>` + d[j] + `</td>`;
					}
					tbody += `<tr>` + str + `</tr>`;
				}
				tbody = `<tbody>` + tbody + `</tbody>`;
				var all = thead + tbody;
				$('#tableMy').empty();
				$('#tableMy').append(all);

				var table = layui.table;
				table.init('demoMy', {
					page: true,
					limit: 10,
					id: 'demoMy',
				});

				$('#dl1').unbind('click').bind('click', function() {
					table.exportFile(arr_title, data, 'xls');
				})
			},


			fillTableJP(data) {
				var thead = '';
				var a = 0;
				var arr_title = [];
				for (var i in data[0]) {
					thead += `<th lay-data="{field:` + a + `}">` + i + `</th>`;
					a++;
					arr_title.push(i)
				}
				thead = `<thead><tr>` + thead + `</tr></thead>`;

				var tbody = '';
				for (var i in data) {
					var d = data[i];
					var str = '';
					for (var j in d) {
						str += `<td>` + d[j] + `</td>`;
					}
					tbody += `<tr>` + str + `</tr>`;
				}
				tbody = `<tbody>` + tbody + `</tbody>`;
				var all = thead + tbody;
				$('#myTableJP').empty();
				$('#myTableJP').append(all);

				var table = layui.table;
				table.init('demoJP', {
					page: true,
					limit: 10,
					id: 'demoJP',
				});

				$('#dl2').unbind('click').bind('click', function() {
					table.exportFile(arr_title, data, 'xls');
				})
			}





		},


	}
</script>

